import React, { useEffect, useState } from "react";
import StoreList from "../containers/StoreList";
import Loading from "../components/Loading";
import { findAllstore } from "../helpers";
import { Action } from 'grommet-icons' 
import { Box } from 'grommet' 
const Home = ({ test }) => {
  const [stores, setStores] = useState([]);
  useEffect(() => {
    findAllstore().then((e) => setStores(e));
  }, []);
  return (
    <>
      {
        !!stores.length  && <StoreList publicPage stores={stores} />
      }
      {
        !stores.length  && <Loading />
      }
    </>
    
  );
};

export default Home;
